.row{
	display:flex;
	flex-wrap:wrap;
}
.col-lg-12{
	-ms-flex:0 0 100%;
	flex:0 0 100%;
	max-width:100%
}
.order-lg-1{-ms-flex-order:1;order:1}
.order-lg-2{-ms-flex-order:2;order:2}
.order-lg-3{-ms-flex-order:3;order:3}
.order-lg-4{-ms-flex-order:4;order:4}
.order-lg-5{-ms-flex-order:5;order:5}
.order-lg-6{-ms-flex-order:6;order:6}
.order-lg-7{-ms-flex-order:7;order:7}
.order-lg-8{-ms-flex-order:8;order:8}

.col-md-6{
	-ms-flex:0 0 50%;
	flex:0 0 50%;
	max-width:50%
}
.ss-text h4, .ss-pic h4{
	font-size:1.5rem
}

.services-container{
	padding: 0 15px;
	width: 100%;
	margin-right: auto;
	margin-left: auto;
	min-width:992px
}

.services-section h2,
.services-section h4{
	margin: 0;
	color: #111111;
	font-weight: 400;
	font-family: "Oswald", sans-serif;
}

.services-section h2 {
	font-size: 36px;
}

.services-section h4 {
	font-size: 24px;
}

.services-section p {
	font-size: 14px;
	font-family: "Muli", sans-serif;
	color: #a9a9a9;
	font-weight: 400;
	line-height: 24px;
	margin: 0 0 15px 0;
}

.services-section img {
	max-width: 100%;
	vertical-align: middle;
	border-style: none;
}

.services-section {
	background: #151515;
}

.spad {
	padding-top: 100px;
	padding-bottom: 100px;
	display: block;
}

.section-title {
	margin-bottom: 45px;
	text-align: center;
}

.section-title span {
	font-size: 16px;
	color: #f36100;
	text-transform: uppercase;
	font-weight: 700;
}

.section-title h2 {
	color: #ffffff;
	font-size: 32px;
	font-weight: 600;
	text-transform: uppercase;
	margin-top: 8px;
}

.ss-text {
	background: #252525;
	height: 100%;
	position: relative;
	z-index: 1;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s;
	display: flex;
	align-items: center;
	justify-content: center;
}
.ss-text div{
	width: 80%;
}

.ss-text:hover {
	background: #363636;
}

.ss-text:hover a {
	color: #f36100;
}

.ss-text:hover:after {
	background: #363636;
}

.ss-text:after {
	position: absolute;
	left: -15px;
	top: 50%;
	height: 20px;
	width: 20px;
	background: #252525;
	content: "";
	-webkit-transform: rotate(45deg) translateY(-10px);
	-ms-transform: rotate(45deg) translateY(-10px);
	transform: rotate(45deg) translateY(-10px);
	z-index: -1;
	-webkit-transition: all 0.2s;
	-o-transition: all 0.2s;
	transition: all 0.2s;
}

.ss-text h4 {
	font-size: 22px;
	color: #ffffff;
	font-weight: 600;
	margin-bottom: 10px;
}

.ss-text p {
	margin-bottom: 11px;
}

.ss-text a {
	font-size: 14px;
	color: #ffffff;
	font-weight: 700;
	text-transform: uppercase;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s;
	text-decoration: none;
}
.ss-text a:hover {
	text-decoration: underline;
}

.ss-text.second-row:after {
	left: auto;
	right: 0;
}

.col-lg-3{
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-bottom: 20px;
}
.p-0{
	padding: 0;
}
@media only screen and (min-width: 1200px) {
	.services-container {
		max-width: 1170px;
	}
}
@media (min-width: 992px) {
	
	.order-lg-4 {
	  -ms-flex-order: 4;
	  order: 4;
	}
	.col-lg-3 {
		-ms-flex: 0 0 25%;
		flex: 0 0 25%;
		max-width: 25%;
	}
}
@media (min-width: 992px) and (max-width: 1199px) {
	.services-container{
	  max-width: 960px;
	}
}
@media only screen and (max-width: 991px) {
	.services-container{
		min-width: auto;
		max-width: 720px;
	}
	.ss-pic img {
		min-width: 100%;
	}
}
@media only screen and (min-width: 768px) and (max-width: 991px) {
	.ss-text:after {
		display: none;
	}
	.ss-text {
		margin-bottom: 30px;
	}
	.col-md-6 {
		-ms-flex: 0 0 50%;
		flex: 0 0 50%;
		max-width: 50%;
	}
}

@media only screen and (max-width: 767px) {
	.order-lg-6{-ms-flex-order:5;order:5}
	.order-lg-5{-ms-flex-order:6;order:6}
	.order-lg-8{-ms-flex-order:7;order:7}
	.order-lg-7{-ms-flex-order:8;order:8}
	.ss-text:after {
		display: none;
	}
	.ss-pic img {
		height: auto;
	}
	.col-lg-3{
		margin-bottom: 0;
	}
	.row{
		flex-direction: column;
		width: 100%;
	}
	.col-lg-3{
		max-width: 100%;
	}
	.ss-text{
		width: 100%;
		height: 293px;
		margin-bottom: 20px;
	}
}
@media (min-width: 576px) and (max-width: 767px){
	.container, .container-sm {
	  max-width: 540px;
	}
}